import React, { useState, useEffect } from 'react';
import { Transition } from "react-transition-group";
import classnames from 'classnames';
import PT from 'prop-types';
import { Link } from 'gatsby';
import styles from './layout.module.scss';
import MenuCategories from "./MenuCategories";
import Input from './Input';
import Button from './Button';
import { MdClear } from "react-icons/md";
import Loader from "./three-dots.svg";

const parseBlogData = (str) => {
  try {
    return JSON.parse(str);
  } catch {
    return {}
  }
}

const fetchUrl = 'https://ve87i5rml1.execute-api.eu-central-1.amazonaws.com/dev/blog-newsletter';

const boxStyles = {
  entering: {
    transform: 'translateY(100%)',
    bottom: 0,
    position: 'fixed',
  },
  entered: {
    transition: 'transform 1000ms ease-in-out',
    bottom: 0,
    position: 'fixed',
    transform: 'translateY(0)',
  },
  exiting: {
    transition: 'transform 1000ms ease-in-out',
    bottom: 0,
    position: 'fixed',
    transform: 'translateY(100%)',
  },
  exited: {
    position: 'static',
  }
}


const NewsletterBox = () => {

  const [email, setEmail] = useState("");
  const [name, setName] = useState("");

  const [fixed, setFixed] = useState(false);
  const [isFetching, setIsFetching] = useState(false);

  const blogDataString = window.localStorage.getItem('blogData') || "{}";
  const { isSubscribed = false } = parseBlogData(blogDataString);

  useEffect(() => {
    const blogDataString = window.localStorage.getItem('blogData') || "{}";
    const { hasClearedNewsletterBox = false } = parseBlogData(blogDataString);
    let tmt = null;

    if (!hasClearedNewsletterBox) {
      tmt = setTimeout(() => setFixed(true), 7000);
    }
    
    return () => clearTimeout(tmt);
  }, []);

  const onSubmitHandler = (e) => {
    e.preventDefault();
    
    setIsFetching(true);

    fetch(fetchUrl, {
      method: 'POST',
      body: JSON.stringify({
        email,
        name: name.length === 0 ? undefined : name
      })
    }).then((resp) => {
      setIsFetching(false);

      if (resp.ok) {
        window.localStorage.setItem('blogData', JSON.stringify({
          hasClearedNewsletterBox: true,
          isSubscribed: true,
        }));
      }
    })
  }

  const handleClose = () => {
    setFixed(false);
    const data = JSON.parse(window.localStorage.getItem("blogData") || "{}");
    window.localStorage.setItem("blogData", JSON.stringify({
      ...data,
      hasClearedNewsletterBox: true,
    }));
  }

  return (
    <Transition in={fixed} timeout={{ exit: 1000 }}>
      {
        (state) => {
          return (
            <form onSubmit={onSubmitHandler} className={styles.newsletterBox} style={{ ...boxStyles.default, ...boxStyles[state] }}>
              <div className={styles.header}>
                <h2>Join the Newsletter</h2>

                {
                  fixed && (
                    <div onClick={handleClose}>
                      <MdClear size={20} fill="white" />
                    </div>
                  )
                }
                
              </div>
              <span>Name</span>
              <div style={{ marginBottom: 15 }}>
                <Input
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                  style={{ width: '100%' }}
                  placeholder="Name"
                  disabled={isSubscribed}
                />
              </div>
              <div><span>E-Mail</span></div>
              <div>
                <Input
                  style={{ width: '100%', marginBottom: 30 }}
                  placeholder="E-mail"
                  value={email}
                  onChange={(e) => setEmail(e.target.value)}
                  disabled={isSubscribed}
                />
              </div>
              <div>
                {isSubscribed ? 
                  <div>You are already subscribed</div>
                  :
                  <Button 
                    type="submit"
                    style={{ width: '100%', height: 34, display: 'flex', alignItems: 'center', justifyContent: 'center' }}
                  >
                    {
                      isFetching ? 
                      <img height={14} src={Loader} style={{ marginBottom: 0 }} />
                      :
                      "Subscribe"
                    }
                  </Button>
                }
                
              </div>
            </form>
          )
        }
      }
    </Transition>
  );
}

class Layout extends React.PureComponent {
  render() {
    const {
      children,
      className,
      styles: htmlStyles,
    } = this.props;

    return (
      <div>
        <div className={classnames(styles.root_panel, className)} styles={htmlStyles}>
          <div className={styles.grid_menu}>
            <div className={classnames(styles.menu, styles.left_menu)}>
                <Link to="/" className={styles.menu_item} activeClassName={styles.active}>
                  Blog
                </Link>
                <Link to="/calculators" className={styles.menu_item} activeClassName={styles.active}>
                  Tools
                </Link>
              <MenuCategories />
            </div>
            <div className={classnames(styles.menu, styles.right_menu)}>
              <Link to="/portfolio" className={styles.menu_item} activeClassName={styles.active}>
                Portfolio
              </Link>
              <Link to="/cv" className={styles.menu_item} activeClassName={styles.active}>
                CV
              </Link>
            </div>
          </div>
          {children}
        </div>
        <footer>
          <NewsletterBox />
        </footer>
      </div>
      
    );
  }
}

Layout.propTypes = {
  children: PT.node.isRequired,
  className: PT.string,
  styles: PT.objectOf(PT.oneOfType([PT.number, PT.string])),
};

Layout.defaultProps = {
  className: '',
  styles: {},
};

export default Layout;
